import React, { Fragment } from 'react';
import LazyLoad from 'vanilla-lazyload';

const shouldShowUrl = (scrollPosition, top, height) => {
    return (scrollPosition + 200 > top) && ((top + height) > (scrollPosition - window.innerHeight - 500))
}

if (!window.lazyLoadInstance) {
  window.lazyLoadInstance = new LazyLoad({ elements_selector: ".lazy" });
}

const GalleryImage = ({ index, left, top, photo, scrollPosition }) => {
    const { width, height, project, name, ...props } = photo;
    const src = `https://demkina.me/images/compressed/${project.id}/${name}`;
    return <Fragment>
        <div className="gallery-image square">
            <div className="gallery-image-wrapper">
                <img
                    className="img lazy"
                    id={name}
                    {...props}
                    alt={`Изображение из серии "${project.title}"`}
                    data-src={src}
                />
            </div>
        </div>
    </Fragment>
}

export default GalleryImage;
