import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { projects } from '../api/projects';


import 'bootstrap/dist/css/bootstrap.css';
import $ from 'jquery';
import bootstrap from 'bootstrap';
import {withRouter} from "react-router";


class Header extends Component {
    render() {

        const keys = Object.values(projects).sort((a, b) => a.order > b.order);
        if (this.props.location.pathname === '/') return null
        return (
            <nav className="navbar navbar-expand-md navbar-light">
                <Link className="navbar-brand" to="/">Nadia Demkina</Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" 
                        data-target="#navbar" aria-controls="navbar" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-end" id="navbar">
                    <div className="navbar-nav">
                        <NavLink exact className="nav-item nav-link" activeClassName="active" to="/">Home</NavLink>
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Projects
                            </a>
                            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownMenuLink">
                                {keys.map(project => <Link key={project.id} className="dropdown-item" to={`/projects/${project.id}`}>{project.title}</Link>)}
                                <Link className="dropdown-item" to="/">Between the lines</Link>
                            </div>
                        </li>
                        <NavLink className="nav-item nav-link" activeClassName="active" to="/about">Bio</NavLink>
                        {/* <NavLink className="nav-item nav-link" activeClassName="active" to="/contact">Contact</NavLink> */}
                    </div>
                </div>
            </nav>
        );
    }
}

export default withRouter(Header);
